import React, { useState } from 'react'
import './Header.scss'


// import logo from '../../assets/images/logo.png'

// import closeIcn from '../../assets/images/close-icn.svg'
// import menuIcn from '../../assets/images/menu-icn.svg'


// import walletIcn from '../../assets/images/social-icons/wallet-icon.svg'
import instaIcn from '../../assets/social-icons/insta.png'
import twitterIcn from '../../assets/social-icons/twitter.png'
import discordIcn from '../../assets/social-icons/discord.png'
import telegramIcn from '../../assets/social-icons/telegram.png'
// import logoWhite from '../../assets/logo-white.png'

import instaWhite from '../../assets/social-icons/insta-white.png'
import logo from '../../assets/logo.png'
import logoMobile from '../../assets/logolarge.webp'

function Header() {

    
    const[headerMobileOpen,setHeaderMobileOpen]=useState(false)
    const openLink=(link)=>{
        // window.open(link)

    }




    const navigateTo=(location)=>{
      
       if(window.innerWidth<1080&&location!=="top"){
        openNavBar()
       }

        var height = 70 ;
        var container = document.getElementsByClassName("App")[0];
        var offsetTop;

        switch (location) {
            case "home":
                
                window.scroll({ top: 0, left: 0, behavior: "smooth" });
              break;
    
       
            case "team":
                offsetTop =document.getElementById("team").offsetTop - height;
                window.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
              break;
            case "founder":
                offsetTop =document.getElementById("king-Nightclub-wraper").offsetTop - height;
                window.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
              break;
            case "mint":
                offsetTop =document.getElementsByClassName("mint-section")[0].offsetTop - height;
                window.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
                break;
            case "faq":
                offsetTop =document.getElementById("faq").offsetTop - 100;
                window.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
                break;
            default:
              break;
        }
    
    }
    

  

    const openNavBar=()=>{
        let header=document.getElementsByClassName('header')[0]

        let hamTop=document.getElementsByClassName('hamburger-top')[0]
        let hamBottom=document.getElementsByClassName('hamburger-bottom')[0]
        let mobileNav=document.getElementsByClassName('header-mobile-nav')[0]

        if(!headerMobileOpen){
            hamTop.style.transition='all 250ms ease-in-out'
            hamBottom.style.transition='all 250ms ease-in-out'
            hamTop.style.top='19px'
            hamTop.style.transform='rotate(45deg)'
            hamBottom.style.bottom='19px'
            hamBottom.style.transform='rotate(-45deg)'

            mobileNav.style.display='block'
            setTimeout(()=>{
                mobileNav.style.opacity='1'
            },[15])
            // header.style.backdropFilter='blur(25px)'
        }
        else{
            hamTop.style.transition='transform 100ms ease,bottom 300ms ease-in-out,top 300ms ease-in-out'
            hamBottom.style.transition='transform 100ms ease,bottom 300ms ease-in-out,top 300ms ease-in-out'
            hamTop.style.top='14px'
            hamTop.style.transform='rotate(0deg)'
            hamBottom.style.bottom='14px'
            hamBottom.style.transform='rotate(0deg)'

            mobileNav.style.opacity='0'
            setTimeout(()=>{
                mobileNav.style.display='none'
            },[250])
            // header.style.backdropFilter='blur(3px)'

        }
        setHeaderMobileOpen(!headerMobileOpen)
    }

    return (
        <div className='Header'>
            <div className='header-inner'>
                <div onClick={()=>navigateTo('top')} className='Header-logo'>
                    <img src={logo} alt='logo'/>
                    <span className='font-header-logo text-white ffam-shoulders weight-semi-bold'>NIGHT KING CLUB </span>
                </div>

                <div className='Header-right-nav weight-semi-bold'>
                
                    <span onClick={()=>navigateTo('home')}  className='ml-1 font-m text-white  ffam-shoulders'>
                        HOME
                    </span>
                    <span onClick={()=>navigateTo('team')}  className='ml-1 font-m text-white   ffam-shoulders' >
                        Team VISION
                    </span>
                    <span onClick={()=>navigateTo('founder')}  className='ml-1 font-m text-white  ffam-shoulders'>
                        FOUNDER STORY
                    </span>
                    <span onClick={()=>navigateTo('')}  className='ml-1 font-m text-white  ffam-shoulders'>
                        DECK
                    </span>
                    <span onClick={()=>navigateTo('faq')}  className='ml-1 font-m text-white  ffam-shoulders'>
                        MINT
                    </span>
                    <span onClick={()=>navigateTo('faq')}  className='ml-1 font-m text-white  ffam-shoulders'>
                        FAQ
                    </span>
                    <div className='social-wrapper'>
                        <span onClick={()=>openLink('')} className='icons'>
                            <i className="fa-brands fa-twitter"></i>
                        </span>

                        <span onClick={()=>openLink('')} className='icons'>
                            <i class="fa-brands fa-telegram"></i>
                        </span>
                    </div>
                </div>
                <button  onClick={()=>{openNavBar()}} className='header-hamburger'>
                    <div className='header-hamburger-inner'>
                        <div className='hamburger-top'></div>
                        <div className='hamburger-bottom'></div>
                    </div>
                </button>
                <div className='header-mobile-nav'>
                    <div className='header-mobile-nav-inner'>
                        <img src={logoMobile} className='header-mobile-logo'/>
                        <button className='font-l ffam-shoulders text-white mobile-nav-button' onClick={()=>{navigateTo('home')}}>HOME</button>
                        <button className='font-l ffam-shoulders text-white mobile-nav-button' onClick={()=>{navigateTo('team')}}>TEAM VISION</button>
                        <button className='font-l ffam-shoulders text-white mobile-nav-button' onClick={()=>{navigateTo('founder')}} >FOUNDER STORY</button>

                        <button className='font-l ffam-shoulders text-white mobile-nav-button' onClick={()=>{navigateTo('')}} >DECK</button>
                        <button className='font-l ffam-shoulders text-white mobile-nav-button' onClick={()=>{navigateTo('')}}>MINT</button>
                        <button className='font-l ffam-shoulders text-white mobile-nav-button' onClick={()=>{navigateTo('faq')}}>FAQ</button>

                        
                        
                        <button onClick={()=>openLink('')} className='font-l ffam-shoulders text-white mobile-nav-button'>
                            TWITTER
                            <img className='header-discord-icn' src={twitterIcn}></img>
                        </button>

                        <button onClick={()=>openLink('')} className='font-l ffam-shoulders text-white mobile-nav-button'>
                            TELEGRAM
                            <img className='header-twitter-icn' src={telegramIcn}></img>
                        </button>
                
                    </div>
                </div>
            </div>
           
            
        </div>
    )
}

export default Header
