import React from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import './Team.css'

const Contents = [
  {
    name: 'KT',
    description: 'Ex-Google engineer with extensive Web 3 development experience. KT met his beautiful model girlfriend at Night King’s party. A proud BAYC and MAYC holder.',   
  },
  {
    name: 'Mike',
    description: 'Ex-Blizzard veteran game designer and amateur DJ. Mike enjoys world-class parties and gorgeous people. A proud BAYC holder.',   
  },
  {
    name: 'Gordan',
    description: 'Ex-investment banker turned crypto/NFT investor. Gordan adores high-end parties and fashion. Knows how to develop unique, innovative strategies to benefit the genuine long-term holders. A proud BAYC holder.',   
  },
  {
    name: 'Stella',
    description: 'Extensive experience in the model agency business, served as a professional model in her early career, and has an expansive network of international professional models. Stella only attends the most exclusive global parties.',   
  },
  {
    name: 'Christina',
    description: 'Expertise in community building/ management and online marketing/partnerships. Christina will focus on making Night King Club the best community in the NFT world—one which everyone desires to join.',   
  },
  {
    name: 'Anna',
    description: 'Extensive event-planning experience, including Masquerade mask parties with 1000+ attendees and 30+ luxurious yacht parties. Anna always ensures that even the most introverted gent has the best time at events.',   
  },
]

const Team = () => {

  return (
    <div id='team'  >
      <Container>
        <div className='teamsEffect king-Nightclub-wraper p-md-5'>
          <h1 className='teams mainHeading pb-5'><span className='text-yellow'>Meet the Night King Club Dream team</span></h1>
          <Row>
            { Contents.map((content, index) => {
              return (
                <Col className="col-12 col-md-6 col-lg-4">
                  <div className='team-card' style={{height: "100%"}}>
                    <div className='team-profile-box'>
                      <img className='team-avater' src={`/images/teams/${index + 1}.webp`} alt="" />
                      <div className='blank'/>
                    </div>
                    <div className='team-des first-4 px-md-5 pt-md-4' style={{height: "calc(100% - 250px)"}}>
                      <p className="member-name">{content.name}</p>
                      <p>{content.description}</p>
                    </div>
                  </div>
                </Col>
              )
            })}            
          </Row>
        </div>
      </Container>
    </div>
  );
};

export default Team;