import React from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import './Review.css'
import { Swiper, SwiperSlide } from "swiper/react"
import {Autoplay, Pagination } from "swiper"
import "swiper/css/pagination";

const Contents = [
  {
    title1: 'Making money without living life is meaningless. Night King always knows how to have the most fun.',
    tip1: 'Pete, Crypto Whale',
    title2: 'If you are passionate about travel, great parties, and making money, this group of people is one you should hang out with.',
    tip2: 'Max, Web 2.0 Millionaire',
    image: 'images/user1.webp'
  },
  {
    title1: 'Night King’s parties are always jam-packed with gorgeous people, even from a plastic surgeon’s perspective.',
    tip1: 'Kim, Seoul Plastic Surgeon',
    title2: 'It’s a pity they do not accept VC investments. The value that Night King Club is creating here is insane.',
    tip2: 'Alex, Venture Capitalist',
    image: 'images/user2.webp'
  },
  {
    title1: 'I get invited to high-end luxurious parties all the time, but Night King’s parties are the ones I make sure I never miss.',
    tip1: 'Ella, Hollywood Actress',
    title2: 'I met my model girlfriend at one of the Night King’s parties. That’s all I have to say.',
    tip2: 'Ryan, Google Engineer',
    image: 'images/user3.webp'
  },
  {
    title1: 'Night King’s parties inspire my creative vision.',
    tip1: 'Maiko, Tokyo Artist',
    title2: 'Besides having tons of fun with him, I met my business partners and investors through my friend, Night King.',
    tip2: 'David, Web 3.0 Builder',
    image: 'images/user4.webp'
  },
]

const Review = () => {

  return (
    <div>
      <Container>
        <h1 className='mainHeading'>What are people saying about<span className='text-yellow'> Night King Club?</span></h1>
        <div>
          <Swiper 
            autoplay={{
              delay: 7000,
              disableOnInteraction: false,
            }}
            pagination={{
              clickable: true,
            }}
            modules={[Autoplay, Pagination]}
            className="mySwiper"
          >
            {Contents.map((content,index) => {
              return (
                <SwiperSlide key={index}>
                  <Row className='m-0 p-3 p-md-5 review-slider'>
                    <Col className='col-12 col-md-6'>
                      <div>
                        <div className='review-card p-4 pt-5 p-md-5 pb-md-4 my-4'>
                          <span className='coma pe-md-5'>❛❛</span>
                          <div  className='pe-md-5 '>
                            <p>{content.title1}</p>
                          </div>
                          <i className='pt-3 pb-md-1 fw-bold'>{content.tip1}</i>
                        </div>
                        <div className='review-card p-4 pt-5 p-md-5 pb-md-4 my-4 mt-md-5 '>
                          <span className='coma pe-md-5'>❛❛</span>
                          <div  className='pe-md-5 '>
                            <p>{content.title2}</p>
                          </div>
                          <i className='pt-3 pb-md-1 fw-bold'>{content.tip2}</i>
                        </div>
                      </div>
                    </Col>
                    <Col className='col-12 col-md-6'>
                      <img className='img-fluid review-user ps-md-5 ' src={content.image} alt="" />
                   
                    </Col>
                  </Row>
                </SwiperSlide>
              )
            })}            
          </Swiper>
        </div>
      </Container>
    </div>
  );
};

export default Review;