import React from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import './MeetWithClub.css'
import { Backdrop, Box, Fade, Modal } from '@mui/material'
import { Swiper, SwiperSlide } from "swiper/react"
import {Autoplay, Pagination } from "swiper"
const Cities = [
  {
    name: 'Tokyo, Japan',
    image: 'tokyo.webp',
  },
  {
    name: 'London, UK',
    image: 'london.webp',
  },
  {
    name: 'New York, USA',
    image: 'newyork.webp',
  },
  {
    name: 'Hollywood, USA',
    image: 'hollywood.webp',
  },
  {
    name: 'Seoul, South Korea',
    image: 'seoul.webp',
  },
]

const MeetWithClub = () => {
  const [open, setOpen] = React.useState(false)
  const handleOpen = () => setOpen(true)
  const handleClose = () => setOpen(false)
  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width:'auto',
    bgcolor: 'transfarent',
    boxShadow: 24,
    p: 4,
  }

  return (
    <div>
      <Container>
        <div className='king-Nightclub-wraper p-2 p-md-5 meetClub'>
          <Row className='reverse-section'>
            <Col className='col-12 col-md-5 p-5 pt-md-2 king-logo pb-0'>
              <img className='img-fluid meetimg' src="/images/night-king.png" alt="" />
            </Col>
            <Col className='col-12 col-md-7 ps-md-3 p-4 p-md-1 pe-md-3 pb-0'>
              <div className='kingClub-text text-start '>
                <h2>Meet <span className='text-yellow'>Night King Club’s</span> visionary leader</h2>
                <div className='pb-2'>
                  <img className='img-fluid meetimg-mobile' src="/images/night-king.png" alt="" />
                </div>
                <p className='pt-2'>
                  <p>The Night King Club formed its initial roots through a visionary leader, Night King. With just $500 in his pocket, Night King built his fortune through relentless real estate and crypto investments. He attributes his success to <span className='text-yellow'>knowing the right people and taking the right actions at precisely the right moments.</span></p><br />
                  <p>Given his connections in the entertainment and fashion industries, Night King has hosted some of the <span className='text-yellow'>most sought-after global luxury parties,</span> including in New York, Hollywood, London, Tokyo and more. His life of adventure, top business connections and links to beautiful people is the envy of others worldwide.</p><br />
                  <p>Night King and his friends achieved financial freedom early in their careers, a testament to their dedication to success. Their next goal? Ultimately, it is building the most influential NFT community by providing extraordinary experiences to the best people. Now, Night King Club’s dream team is committed to offering <span className='text-yellow'>exclusive membership benefits to all NKG Clubber NFT holders.</span> Are you ready to dive into your next journey?</p><br />
                </p>
              </div>
            </Col>
          </Row>
        </div>
        <Container className="pb-5 ">
          <div className='primary-text-2 px-md-5'>
            <h2 className='py-4'><span className="text-yellow">Have fun with fellow NKG Clubbers around the world</span></h2>
            <p className="text-align-left">Night King Club will hold members-only luxurious parties along with major Crypto/NFT events (e.g., NFT NYC). In this way, <span className="text-yellow">you can benefit from the awe-inspiring experience during the same trip.</span> Furthermore, we partner with <span className="text-yellow">international modeling agencies and professional event planning teams.</span> This means we can ensure your time is spent ONLY at the best-in-class parties containing the most fabulous people. <br/><br/>
              We are also partnering with high-end venues in some major cities worldwide to offer our members further exclusive VIP benefits. <span className="text-yellow">Simply shoot a message in our NFT-gated Discord group when you arrive in a new city.</span> Then, you can hang out with fellow NKG Clubbers at these amazing venues.</p>
          </div>
          {/* <Col className="pb-5">
            { Cities.map((city) => {
              return (
                <div>
                  <img className="city-image pt-4" src={`images/cities/${city.image}`} alt={city.image} />
                  <h1 className="pt-1 text-yellow">{city.name}</h1>
                </div>
              )
            })}
          </Col> */}
          <Swiper 
            autoplay={{
              delay: 7000,
              disableOnInteraction: false,
            }}
            pagination={{
              clickable: true,
            }}
            modules={[Autoplay, Pagination]}
            // className="mySwiper"
          >
            {Cities.map((city,index) => {
              return (
                <SwiperSlide key={index}>
                  <Row className='m-0 p-3 p-md-5 '>
                    <div className='city-wrapper'>
                      <img className="city-image pt-4" src={`images/cities/${city.image}`} alt={city.image} />
                      <h1 className="pt-1 text-yellow">{city.name}</h1>
                    </div>
                  </Row>
                </SwiperSlide>
              )
            })}            
          </Swiper>
        </Container>
        <Container>
          <div className='video-wraper'>
            <div className='modal-video'>
              <img className='img-fluid' src="/images/metaverse-thumbnail.webp" alt="" />
              <button onClick={()=>handleOpen()} className='play-thevideo-btn'><i class="fa-solid fa-play play-icon"></i></button>
            </div>
          </div>
          <Modal
            className="modal-video"
            aria-labelledby="transition-modal-title"
            aria-describedby="transition-modal-description"
            open={open}
            onClose={handleClose}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
              timeout: 500,
            }}
          >
            <Fade in={open}>
              <Box sx={style}>
                <iframe width="560" height="315" src="https://www.youtube.com/embed/1hNdsrJgYHs?controls=0" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
              </Box>
            </Fade>
          </Modal>
          <div className='primary-text px-md-5'>
            <h2 className='mx-md-5 py-4 mt-5 pt-5'><span className="text-yellow">NKG Metaverse: Redefining WEB 3.0 high-end networking</span></h2>
            <p className='px-md-5 mx-md-5 pb-4 text-align-left'>Besides meeting others and vibing at some of the most exclusive real-world parties, Night King Club is also creating an innovative NKG metaverse for high-end virtual networking. Here, you will be able to <span className="text-yellow">meet new acquaintances, play exciting games and unwind with your friends at various luxurious nightclub scenes</span> in the NKG metaverse.<br/><br/>
            Meanwhile, our $NKG utility token can be used to purchase valuable in-metaverse NFTs to unlock exclusive, not-to-be-missed experiences. 😉</p>
          </div>
        </Container>
      </Container>
    </div>
  );
};

export default MeetWithClub;