import React from 'react';
import { Col, Container, NavLink, Row } from 'react-bootstrap';
import logo from '../../assets/logo.png'
import './Footer.scss'
import arrowUp from '../../assets/arrow-up.png'

const Footer = () => {


    const openLink=(link)=>{
        // window.open(link)

    }

    const navigateTo=(location)=>{

        var height = 70 ;
        var container = document.getElementsByClassName("App")[0];
        var offsetTop;

        switch (location) {
            case "home":
                
                window.scroll({ top: 0, left: 0, behavior: "smooth" });
              break;
    
       
            case "team":
                offsetTop =document.getElementById("team").offsetTop - height;
                window.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
              break;
            case "founder":
                offsetTop =document.getElementById("king-Nightclub-wraper").offsetTop - height;
                window.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
              break;
            case "mint":
                offsetTop =document.getElementsByClassName("mint-section")[0].offsetTop - height;
                window.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
                break;
            case "faq":
                offsetTop =document.getElementById("faq").offsetTop - 100;
                window.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
                break;
            default:
              break;
        }
    
    }
    

    return (
        <div className='mt-5 pt-5'>
           <div className='footer p-5 mt-5 footer-logo'>
                <div className='footer-top '>
                    <div className="">
                        <div className='d-flex align-items-center '>
                            <img className=' img-fluid me-4' src={logo} alt="" />
                            <h4 className='text-yellow mt-2'>NIGHT KING CLUB</h4>
                        </div>
                    </div>
                    <div className='footer-top-right'>
                       
                        <div className="footer-links text-start ps-md-5 ms-md-5">
                            <h5>Quicks Links</h5>
                            <p onClick={()=>{navigateTo('home')}}><small>Home</small></p>
                            <p onClick={()=>{navigateTo('team')}}><small>Team Vision</small></p>
                            <p onClick={()=>{navigateTo('founder')}}><small>Founder Story</small></p>
                            <p><small>Deck</small></p>
                            <p><small>Mint</small></p>
                            <p onClick={()=>{navigateTo('faq')}}><small>Faq</small></p>
                        </div>
                    

                        <div className='footer-social-wrapper  text-start'>
                            <h5>Social Media</h5>
                            <div>
                                <small><i class="fa-brands fa-telegram footer-icon"></i> Telegram</small> <br />
                                <small><i class="fa-brands fa-twitter footer-icon"></i> Twitter</small>
                            </div>
                        </div>
                    
                    </div>
                  
                </div>
                <Container className='mt-5 dividers'>
                    <a href="#top">
                        <button className='upScrool'>
                            <img src={arrowUp}/>
                        </button>
                    </a>
                    <hr />
                    <p className='copyright'>&copy; Copyright 2022 Night King Club All Rights Reserved</p>
                </Container> 
           </div>
         
        </div>
    );
};

export default Footer;