import React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import './KingClub.scss'
import logo from '../../../assets/logolarge.webp'
// import collage from '../../../assets/collage.png'
import nftVideo from '../../../assets/videos/nfts.mp4'

const KingClub = () => {
  return (
    <div>
      <Container>
        <div id="king-Nightclub-wraper"  className='king-Nightclub-wraper after-effect p-2 p-md-5'>
          <Row>
            <Col className='col-12 col-md-5 p-5 pt-md-2 king-logo align-self-center'>
              <img className='img-fluid' src={logo} alt="" />
            </Col>
            <Col className='col-12 col-md-7 ps-md-3 p-4 p-md-1 pe-md-3'>
              {/* <div className='kingClub-text text-start '> */}
              <div className='kingClub-text'>
 
                <h2 >
                  <span className='text-yellow'>Night King Club: the high-end NFT global social club you simply do not want to miss</span>
                </h2>
                <div className='pt-2'>
                  <p>“To succeed in the crypto/NFT world, it’s all about whom you hang out with.” - Night King</p><br />
                  <p>With BAYC holders as co-founders, Night King Club is a high-end NFT global social club committed to bringing <span className='text-yellow'>world-class luxury parties</span> to blue-chip NFT holders, business elites and unique talents.</p><br />
                  <p><span className='text-yellow'>Having an amazing time together at great parties</span> is the best way to build lasting connections. At Night King Club, members will share their unique talents, expertise, and <span className='text-yellow'>ALPHA</span> to <span className='text-yellow'>assist each other in achieving even more in life.</span></p><br />
                </div>
              </div>
            </Col>
            <Col className='col-12 col-md-7 p-4 pe-md-5 mt-md-5 pt-md-5'>
              <div className='kingClub-text pt-4'>
                <h2><span className='text-yellow'>The dream NFT you have been waiting for: NKG Clubbers are the keys to the high-end NFT social circle</span></h2>
                <p className='pt-3'>NKG Clubbers are a collection of 5000 utility-enabled PFPs with unique designs and utilities that cannot be found elsewhere. All NKG clubbers can unlock exclusive Night King Club benefits, which further grant membership to the most exclusive high-end NFT global social club. The longer you hold and stake, the better the rewards.</p>
                {/* <button className='long-button mt-4'>Get your NKG Clubber NFT now!</button> */}
              </div>
            </Col>
            <Col className='col-12 p-4 col-md-5 mt-md-5 align-self-center'>
              <div className='collage'>
                {/* <img className='img-fluid' src={collage} alt="" /> */}
                <video  preload='none' autoPlay playsInline muted loop className='king-club-video' src={nftVideo}/>
              </div>
            </Col>
          </Row>
        </div>
      </Container>
    </div>
  );
};

export default KingClub;