import React from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import './PartnerShip.scss'
import nftVideo from '../../../assets/videos/event.mp4'

const Contents = ['google', 'goldman', 'blizzard', 'meta', 'img', 'ford']

const PartnerShip = () => {

  return (
    <div id='PartnerShip'>
      <Container>
        <div className='p-md-5'>
          <h2><span className="text-yellow">A social club only for high quality people</span></h2>
          <Container>
            <div className='primary-text px-md-5 pt-5'>
              <p className='mx-md-5 py-4 pb-5 text-align-left'><span >Our founding members are all bullish about the future of NFTs and have experience in some of the world-leading firms, modeling agencies, and other renowned talent centers.</span></p>
            </div>
            <Row className="pb-5">
              { Contents.map((content, index) => {
                return (
                  <Col key={index} className="col-6 col-sm-6 col-md-4 col-lg-4 p-1">
                    <img className="image" src={`images/partnership/${content}.webp`} alt={`partnership${index}`} />
                  </Col>
                )
              })}
            </Row>
          </Container>
          <h2><span className="text-yellow">A social club that makes your dream parties come true</span></h2>
          <div className='primary-text px-md-5 pt-5'>
            <p className='mx-md-5 py-4 pb-5 text-align-left'>
              <span >Have fantasies about the great parties you want to attend before you die? We got you.
                As a NKG clubber, you can submit the party/event idea you always have anonymously in our private Discord channel (e.g., Mysterious Masquerade party in Paris, Summer model bikini party on an exotic beach, Costume party in Japan with fireworks…) Our professional team will select some of the best ideas and make it a reality. 
              </span>
            </p>
          </div>
          <div className='event-video-wrapper' >
            <video  preload='none' autoPlay playsInline muted loop className='king-club-video' src={nftVideo}/>
          </div>
        </div>
      </Container>
    </div>
  );
};

export default PartnerShip;